html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #151519;
  color: #FFFFFF;
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}
@media (min-width: 768px) {
  body {
    height: 100%;
  }
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  color: #FFFFFF;
  text-align: center;
}

h1 {
  font-size: 6rem;
}

h2 {
  font-size: 5.6rem;
}

h3 {
  font-size: 4rem;
  text-align: center;
}

p {
  color: #FFFFFF;
  font-size: 2rem;
}

.nombre-pagina {
  margin: 5rem 0;
  font-size: 6rem;
  overflow: hidden;
  text-wrap: wrap;
  text-overflow: ellipsis;
}

.descripcion-pagina {
  text-align: center;
}

@media (min-width: 768px) {
  .contenedor-app {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    min-height: 100vh;
  }
}
.contenedor-app .imagen {
  height: 20rem;
  background-image: url("../img/background2.jpg");
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
}
@media (min-width: 480px) {
  .contenedor-app .imagen {
    height: 30rem;
  }
}
@media (min-width: 768px) {
  .contenedor-app .imagen {
    height: auto;
    background-position-x: -42rem;
  }
}
.contenedor-app .app {
  width: 100%;
  margin: 0 auto;
  padding: 3rem;
}
@media (min-width: 768px) {
  .contenedor-app .app {
    overflow-y: auto;
  }
}

.barra {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .barra {
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-top: 0;
    gap: 0 2rem;
  }
}
.barra .saludo {
  font-weight: bold;
}
.barra .nombre {
  color: #a5dc86;
}
.barra .boton {
  background-color: #e06667;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 5rem 0;
}
@media (min-width: 768px) {
  .barra .boton {
    width: auto;
  }
}
.barra .boton:hover {
  background-color: #d73c3d;
  cursor: pointer;
}

.barra-servicios {
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .barra-servicios {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    gap: 2rem;
  }
}
.barra-servicios .boton {
  min-width: 25rem;
  background-color: #e0c766;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 5rem 0;
  margin: 0 0 1rem 0;
}
@media (min-width: 768px) {
  .barra-servicios .boton {
    width: auto;
  }
}
.barra-servicios .boton:hover {
  background-color: #d7b73c;
  cursor: pointer;
}
@media (min-width: 768px) {
  .barra-servicios .boton {
    margin: 0;
  }
}

.seccion {
  padding: 5rem 0;
  display: none;
}
.seccion .text-center {
  text-align: center;
}

.mostrar {
  display: block;
}

.ocultar {
  display: none;
}

.listado-servicios {
  display: grid;
  gap: 2rem;
}
@media (min-width: 768px) {
  .listado-servicios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.listado-servicios .servicio {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: #FFFFFF;
  padding: 3rem;
  border-radius: 1rem;
  transition: transform 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  text-align: center;
}
.listado-servicios .servicio:hover {
  transform: scale(1.05);
}
.listado-servicios .servicio p {
  color: #1a1b15;
  margin: 0 0 1.25rem 0;
  user-select: none;
}
.listado-servicios .servicio .precio-servicio {
  color: #E69F59;
  font-weight: 700;
  font-size: 3rem;
  margin: 0;
}
.listado-servicios .servicio.seleccionado {
  filter: brightness(50%);
  transform: scale(0.95);
}

.contenido-resumen p {
  margin-bottom: 1rem;
}
.contenido-resumen span {
  color: #78a5ff;
  font-weight: 700;
  font-size: 2.4rem;
}
.contenido-resumen h3:last-of-type {
  margin-top: 2.5rem;
}

.contenedor-servicio {
  border-bottom: 0.2rem dotted #f1f1f1;
}
.contenedor-servicio span {
  color: #fe5544;
}

.citas {
  list-style-type: none;
  padding: 0;
}
.citas li {
  padding: 1rem;
  border-bottom: 1px solid #FFFFFF;
}
.citas li:last-child {
  border-bottom: none;
}
.citas li p {
  font-weight: 700;
  color: #E69F59;
}
.citas li span,
.citas li .servicio {
  font-weight: 400;
  color: #FFFFFF;
}
.citas li .servicio {
  color: #78a5ff;
}
.citas li .precio {
  text-decoration: underline;
  text-decoration-color: #fe5544;
}

.noCitas {
  margin-top: 5rem;
}

.servicios {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(25rem, 1fr));
  gap: 2rem;
  padding: 0;
}
.servicios li {
  position: relative;
  padding: 1.5rem 1.5rem 4rem 1.5rem;
  background-color: cornflowerblue;
  border-radius: 1rem;
  list-style: none;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.servicios p {
  font-weight: 700;
  color: #3a4069;
  margin: 1rem 0;
}
.servicios p span {
  font-weight: 400;
  color: #FFFFFF;
}

.btn {
  position: absolute;
  width: 3rem;
  height: 3rem;
  border-radius: 999px;
  display: block;
  text-align: center;
  line-height: 3rem;
  padding: 0.5rem;
  border: none;
  outline: none;
}

.btn-actualizar {
  bottom: 0.5rem;
  right: 0.5rem;
  background-color: gold;
  color: #FFFFFF;
}

.btn-eliminar {
  bottom: 0.5rem;
  right: 4rem;
  background-color: darkred;
  color: #FFFFFF;
  cursor: pointer;
}

.formulario {
  margin-top: 5rem;
  width: 100%;
}

.campo {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  margin-bottom: 2rem;
}
.campo label {
  flex-basis: 12rem;
  padding: 1rem 0;
}
.campo input {
  width: 100%;
  flex: 1;
  border: none;
  padding: 1.25rem;
  border-radius: 1rem;
  min-width: 22rem;
  outline: none;
  caret-color: #608fff;
}
.campo input:disabled {
  color: #FFFFFF;
  cursor: not-allowed;
}

.campo-password {
  position: relative;
  flex: 1;
}

.icono-ojo {
  width: 2.5rem;
  cursor: pointer;
  margin: 0 1rem;
  align-self: center;
  position: absolute;
  right: 0.2rem;
  top: 50%;
  transform: translateY(-50%);
  background-color: #FFFFFF;
  user-select: none;
  -webkit-user-drag: none;
  -ms-user-select: none;
  -moz-user-select: none;
}

.icono-ojo-animacion {
  animation: fadeUp 300ms cubic-bezier(0.175, 0.885, 0.32, 1.275);
}

@keyframes fadeUp {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.boton {
  background-color: #4b6b9c;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 5rem 0;
}
@media (min-width: 768px) {
  .boton {
    width: auto;
  }
}
.boton:hover {
  background-color: #3a537a;
  cursor: pointer;
}

.boton-eliminar {
  background-color: #e06667;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 5rem 0;
}
@media (min-width: 768px) {
  .boton-eliminar {
    width: auto;
  }
}
.boton-eliminar:hover {
  background-color: #d73c3d;
  cursor: pointer;
}

.acciones {
  text-align: center;
  margin-top: 5rem;
}
.acciones a {
  color: #E69F59;
  transition: 0.3s;
}
.acciones a:hover {
  color: #df852d;
}

.alerta {
  padding: 1.25rem;
  color: #FFFFFF;
  width: 100%;
  display: block;
  margin-bottom: 2rem;
  text-align: center;
  font-weight: 700;
  opacity: 0;
  animation: aparecer 1s cubic-bezier(0.175, 0.885, 0.32, 1.275) forwards 0.1s;
}
.alerta.error {
  background-color: #cb0000;
}
.alerta.exito {
  background-color: #329f00;
}

.swal2-popup {
  font-size: 1.8rem;
}

@keyframes aparecer {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@media (min-width: 768px) {
  .tabs {
    display: flex;
  }
}
.tabs button {
  background-color: #E69F59;
  border: none;
  color: #FFFFFF;
  padding: 1rem;
  width: 100%;
  margin-bottom: 1rem;
  text-transform: uppercase;
  font-weight: 700;
  cursor: pointer;
  transition: background-color 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
}
.tabs button:hover {
  background-color: #e39243;
}
.tabs button.actual {
  background-color: #FFFFFF;
  color: #E69F59;
}

.paginacion {
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .paginacion {
    display: flex;
    justify-content: space-between;
  }
}
.paginacion button {
  margin: 0 0 1rem 0;
  background-color: #E69F59;
}
.paginacion button:hover {
  background-color: #e2903e;
}

.ocultar {
  visibility: hidden;
}